<template>
    <div class="prize">
        <div class="prize-head">
            <!-- <el-date-picker class="time-search"
                            style="width: 230px;"
                            v-model="SearchTimeZone"
                            type="datetimerange"
                            align="right"
                            size="small"
                            :start-placeholder="$L('开始日期')"
                            :end-placeholder="$L('结束日期')"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            :default-time="['00:00:00', '23:59:59']"
                            @change="SearchTimeFunc">
            </el-date-picker> -->
			<el-button size="small" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
            <el-select class="search" v-model="messageType" size="small"
                       @change="changeTypeFunc"v-if="$route.params.hasOwnProperty('type') && $route.params.type !== 'point' && $route.params.type !== 'coupon'">
                <el-option
                        v-for="item in messageTypelist"
                        :key="item.type"
                        :label="item.name"
                        :value="item.type">
                </el-option>
            </el-select>
        </div>
        <div class="prize-content">
            <div class="prize-list" v-if="tableData.list">
                <div class="list-item" v-for="item in tableData.list">
                    <div class="top">
                        <span class="left sn">{{$L('订单号')}}：{{ item.sn }}</span>
                        <span class="right" v-if="$route.params.hasOwnProperty('type') && $route.params.type !== 'point' && $route.params.type !== 'coupon'" :class="stateTypeFunc(item.deliveryStatus,item.status).color">{{stateTypeFunc(item.deliveryStatus,item.status).text}}</span>
                    </div>
                    <div class="main">
                        <div  @click="getInfoFunc(item.id,item.product_type)">
                            <img src="../../../assets/img/prize/jifen.png" alt="" class="img" v-if="$route.params.hasOwnProperty('type') && $route.params.type == 'point'">
                            <img src="../../../assets/img/prize/points_coupon.png" alt="" class="img" v-else-if="$route.params.hasOwnProperty('type') && $route.params.type == 'coupon'">
                            <img :src="item.pic" alt="" class="img" v-else>
                            <p class="name left">{{ item.name }}</p>
                        </div>

                        <div class="from right">
                            <p>{{ fromTypeFunc(item.from).text }}</p>
                            <p v-if="item.type == 1">{{ $L('礼品') }}</p>
                            <p v-else-if="item.type == 2">{{ $L('优惠券') }}</p>
                            <p v-else-if="item.type == 3">{{ $L('积分') }}</p>
                            <p v-else>{{ $L('产品') }}</p>
                            <p class="times">{{item.time}}</p>
                            <el-button size="small" type="primary" v-if="item.status == 0 && item.type !== 2 && item.type !== 3" @click="applyFunc(item.id,item.type,item.from,item.prizeRecordId,item.product_type)">{{ $L('申请领奖') }}</el-button>
                            <el-button size="small" v-if="item.deliveryStatus == 2" type="primary" @click="confirmFunc(item.id)">{{$L('确认收货')}}</el-button>
                            <span v-else-if="item.status == 1  && item.type !== 2 && item.type !== 3">
                                <p class="details mainFontColor" @click="getInfoFunc(item.id,item.product_type)">{{ $L('订单详情') }}</p>
                            </span>
                            <el-button size="small" type="primary" v-if="item.type === 2" @click="goCoupon">{{ $L('查看优惠券') }}</el-button>
                            <el-button size="small" type="primary" v-if="item.type === 3" @click="goPoint">{{ $L('查看积分') }}</el-button>
                        </div>

                    </div>
                </div>
            </div>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
                <el-pagination
                        background
                        small
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10,20,30,40]"
                        :page-size="pageSize"
                        layout="prev, pager, next"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
            <div class="no-data" v-else>{{$L('暂无数据')}}</div>
        </div>
        <transition name="fade">
            <div class="time" v-if="showTime">
                <div class="close" @click="closeTimeFunc"></div>
                <div class="date">
                    <calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
                    <div class="btns">
                        <el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
                        <el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    import exchangeBox from "../../../components/prize/exchangeBox.vue";
    export default {
        name: "prize",
        data(){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                value1: '',
                messageType:'all',
                messageTypelist:[
                    {
                        type:'all',
                        name:this.$L('全部状态')
                    },
                    {
                        type:'applyPrize',
                        name:this.$L('未申请领奖')
                    },
                    {
                        type:'picking',
                        name:this.$L('等待卖家发货')
                    },
                    {
                        type:'noFinish',
                        name:this.$L('卖家已发货，等待确认')
                    },
                    {
                        type:'finish',
                        name:this.$L('订单已完成')
                    },
                ],
                dtCreateStart:'',
                dtCreateEnd:'',
                tableData:{},
                currentPage:1,
                pageSize:10,
				showTime:false,
				timeArr:[],
				defaultDate:[],
            }
        },
        props:{

        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.getData(to);
            next();
        },
        created() {
            this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                if(!router){
                    router = _self.$route;
                }
                _self.loading = true;
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    status : router.params.type ? router.params.type :'prod',
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd,
                    type : _self.messageType
                };
                _self.$api('getPrizeList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            goCoupon(){
                this.$router.push({name:"couponList"});
            },
            goPoint(){
                this.$router.push({name:"integralPoint"});
            },
            getInfoFunc(id,ptype){
                this.$router.push({name:"prizeInfo",params:{id:id,ptype:ptype}});
            },
            changeTypeFunc(val){
                this.messageType = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleSelectionChange(val){
                let arr = [];
                for (let i in val){
                    arr[i] = val[i].id;
                }
                this.msgArr = arr;
            },
            stateTypeFunc(delivery,order){
                let s;
                if(order == -1){
                    s = {text:this.$L("订单已关闭"),color:'green'}
                }else if(delivery == 3){
                    s = {text:this.$L("订单已完成"),color:'green'}
                }else if(delivery == 2){
                    s = {text:this.$L("已发货"),color:'green'}
                }else if(delivery == 1){
                    s = {text:this.$L("配货中"),color:'green'}
                }else if(delivery == 0 && order == 0){
                    s = {text:this.$L("未申请领取"),color:'red'}
                }else if(delivery == 0 && order == 1){
                    s = {text:this.$L("等待配货"),color:'green'}
                }else if(order == 1){
                    s = {text:this.$L("已确认订单"),color:'green'}
                }else if(order == 0){
                    s = {text:this.$L("未领取"),color:'red'}
                }
                return s;
            },
            fromTypeFunc(n){
                let f;
                if(n == 1){
                    f = {text:this.$L("大转盘")}
                }else if(n == 2){
                    f = {text:this.$L("刮刮卡")}
                }
                return f;
            },
            SearchTimeFunc(time){
                if(time !== null){
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                }else{
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            confirmFunc(id){
                const _self = this;
                let pid = id;
                _self.$confirm(_self.$L(' 是否确认收货?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {
                    _self.$api('updatePrizeStatus', {prizeId: pid}, function (reData) {
                        if (reData.flag === 1) {
                            _self.$message({
                                type: 'success',
                                message: _self.$L('确认收货成功!')
                            });
                            _self.loading = false;
                            _self.getData();
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), { //todo:msg还未更换语言包语言包
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                })
            },
            applyFunc(id,type,form,recordId,product_type){
                const _self = this;

                // let pid = id;
                _self.$confirm(_self.$L(' 是否申请领奖?'), _self.$L('提示'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning'
                }).then(() => {

                    this.$store.commit('setDialogObj', {
                        name: this.$L('立即兑换'),
                        view: exchangeBox,
                        width: '94%',
                        height: '600px',
                        data: {
                            id:id,
                            recordId:recordId,
                            ptype:type,
                            form:form,
                            product_type:product_type
                        }
                    });

                })
            },
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.getData();
			},
			gotoTime(){
				this.showTime = false;
				this.getData();
			},

        }
    }
</script>

<style lang="scss" scoped>
    .prize{
        position: absolute;
        .prize-head{
            position: relative;
            width: 100%;
            height: 45px;
            padding: 6px 10px 0;
            background-color: #fff;
            margin: 10px 0;
            .font-middle{
                padding: 0 10px;
            }
            .search{
                width: 30%;
                vertical-align: top;
                margin-left: 10px;
            }
        }
        .prize-content{
            background-color: #fff;
            .prize-list{
                padding: 0 10px;
                .list-item{
                    padding: 15px 0;
                    border-bottom: 1px solid #E1E1E1;
                    overflow: hidden;
                    .left{
                        float: left;
                    }
                    .right{
                        float: right;
                    }
                    .sn{
                        color: #999999;
                    }
                    .red{
                        color: #FF2D52;
                    }
                    .green{
                        color: #00CC34;
                    }
                    .gray{
                        color: #999999;
                    }
                    .top{
                        overflow: hidden;
                        margin-bottom: 15px;
                    }
                    .main{
                        overflow: hidden;
                        .img{
                            vertical-align: middle;
                            float: left;
                            width: 80px;
                            height: 80px;
                        }
                        .name{
                            margin-left: 10px;
                            width: 30%;
                            text-overflow: ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 2;
                            -webkit-box-orient: vertical;
                            overflow: hidden;
                        }
                        .from{
                            color: #666666;
                            text-align: right;
                            .times{
                                margin: 23px 0 10px;
                            }
                        }
                    }
                }
            }
            .list-pages{
                text-align: center;
                padding: 20px 0;
            }
            .no-data{
                position: relative;
                width: 100%;
                height: 170px;
                line-height: 170px;
                text-align: center;
                background-color: #ffffff;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>